import React from 'react';
import about from '../img/about/training6.jpeg'
import pattern from '../img/pattern/pattern.png'
import img_1 from '../img/service/audit.png';
import img_2 from '../img/service/building.jpg';
import img_3 from '../img/service/training.jpg';
import img_4 from '../img/service/training2.jpeg';

const Service = () => {
    return (
        <div className="service_area">
            <div className="container">
                <div className="row">
                    <div className="col-xl-12">
                        <div className="section_title text-center mb-50">
                            <h3>Our Services</h3>
                        </div>
                    </div>
                </div>
                <div className="border_bottom_1px position-relative">
                    <div className="pattern_img d-none d-xl-block">
                        <img src={pattern} alt="" />
                    </div>
                    <div className="row">
                        <div className="col-xl-12">
                            <div className="service_active owl-carousel">
                                <div className="single_service">
                                    <div className="thumb">
                                        <img src={img_1} alt="" />
                                    </div>
                                    <div className="service_info">
                                        <a href="#">
                                            <h3>Certificate of Registration, Licensing and Consent </h3>
                                        </a>
                                        <p>Certificate of Registration, Licensing and Consent to Operate Services for Factories, construction premises.</p>
                                        <a className="d-flex align-items-center" href="#">More <i className="ti-angle-right"></i>
                                        </a>
                                    </div>
                                </div>
                                <div className="single_service">
                                    <div className="thumb">
                                        <img src={img_2} alt=""/>
                                    </div>
                                    <div className="service_info">
                                        <a href="#">
                                            <h3>Building Construction</h3>
                                        </a>
                                        <p>Plans, Procedures and ISO services for Factories, construction premises.</p>
                                        <a className="d-flex align-items-center" href="#">More <i className="ti-angle-right"></i>
                                        </a>
                                    </div>
                                </div>
                                <div className="single_service">
                                    <div className="thumb">
                                        <img src={img_3} alt=""/>
                                    </div>
                                    <div className="service_info">
                                        <a href="#">
                                            <h3>Tangible Engineering and Training Services for Factories, construction</h3>
                                        </a>
                                        <p>Inspection, examination and testing of all lifting tools and tackles and pressure plants.</p>
                                        <a className="d-flex align-items-center" href="#">More <i className="ti-angle-right"></i>
                                        </a>
                                    </div>
                                </div>
                                <div className="single_service">
                                    <div className="thumb">
                                        <img src={img_4} alt=""/>
                                    </div>
                                    <div className="service_info">
                                        <a href="#">
                                            <h3>Authorized training facility offered by PES. </h3>
                                        </a>
                                        <p>A team of certified competent person (Approved by DISH in Maharashtra, Gujrat, Goa, Punjab etc).</p>
                                        <a className="d-flex align-items-center" href="#">More <i className="ti-angle-right"></i>
                                        </a>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        </div>
    
    );
}

export default Service;